.avatar-upload .avatar-section {
  background: white;
  border-radius: 12px;
  padding: 24px;
  border: 1px solid #e5e7eb;
}
.avatar-upload .avatar-section .section-title {
  font-size: 18px;
  font-weight: 600;
  color: #111827;
  margin: 0 0 8px 0;
}
.avatar-upload .avatar-section .section-description {
  font-size: 14px;
  color: #6b7280;
  margin: 0 0 24px 0;
}
.avatar-upload .avatar-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 24px;
}
.avatar-upload .avatar-display {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 16px;
}
.avatar-upload .avatar-circle {
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background: #f3f4f6;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 3px solid #e5e7eb;
  overflow: hidden;
  transition: all 0.3s ease;
}
.avatar-upload .avatar-circle.has-avatar {
  border-color: #3b82f6;
}
.avatar-upload .avatar-circle .avatar-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}
.avatar-upload .avatar-circle .avatar-initials {
  font-size: 36px;
  font-weight: 600;
  color: #6b7280;
}
.avatar-upload .avatar-controls {
  display: flex;
  flex-direction: column;
  gap: 12px;
  align-items: center;
}
.avatar-upload .file-input {
  display: none;
}
.avatar-upload .upload-button,
.avatar-upload .remove-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 10px 16px;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 500;
  border: 1px solid;
  cursor: pointer;
  transition: all 0.2s ease;
}
.avatar-upload .upload-button:disabled,
.avatar-upload .remove-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.avatar-upload .upload-button {
  background: #3b82f6;
  color: white;
  border-color: #3b82f6;
}
.avatar-upload .upload-button:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}
.avatar-upload .remove-button {
  background: white;
  color: #dc2626;
  border-color: #dc2626;
}
.avatar-upload .remove-button:hover:not(:disabled) {
  background: #fef2f2;
}
.avatar-upload .button-icon {
  width: 16px;
  height: 16px;
}
.avatar-upload .button-icon.animate-spin {
  animation: spin 1s linear infinite;
}
.avatar-upload .upload-actions {
  display: flex;
  gap: 12px;
  justify-content: center;
}
.avatar-upload .save-button {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 24px;
  background: #10b981;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.2s ease;
}
.avatar-upload .save-button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.avatar-upload .save-button:hover:not(:disabled) {
  background: #059669;
}
.avatar-upload .error-message {
  display: flex;
  align-items: center;
  gap: 8px;
  padding: 12px 16px;
  background: #fef2f2;
  border: 1px solid #fecaca;
  border-radius: 8px;
  color: #dc2626;
  font-size: 14px;
  margin-top: 16px;
}
.avatar-upload .error-message .error-icon {
  width: 16px;
  height: 16px;
  flex-shrink: 0;
}
.avatar-upload .help-text {
  margin-top: 16px;
  padding-top: 16px;
  border-top: 1px solid #e5e7eb;
}
.avatar-upload .help-text p {
  font-size: 12px;
  color: #6b7280;
  margin: 4px 0;
}

@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 640px) {
  .avatar-upload .avatar-section {
    padding: 16px;
  }
  .avatar-upload .avatar-circle {
    width: 100px;
    height: 100px;
  }
  .avatar-upload .avatar-circle .avatar-initials {
    font-size: 28px;
  }
  .avatar-upload .avatar-controls {
    flex-direction: column;
    width: 100%;
  }
  .avatar-upload .upload-button,
  .avatar-upload .remove-button {
    width: 100%;
    justify-content: center;
  }
}
